:root {
  --primary-color: #04619f;
  --secondary-color: #000000;
  --font-color: #555;
  --background: linear-gradient(
    45deg,
    var(--primary-color),
    var(--secondary-color)
  );
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    /* background: black; */
  }
  to {
    opacity: 1;
    /* background: linear-gradient(45deg, #000, #555); */
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    /* background: black; */
  }
  to {
    opacity: 1;
    /* background: linear-gradient(45deg, #000, #555); */
  }
}
@-webkit-keyframes wait {
  from {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}
@keyframes wait {
  from {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}
.background {
  background: linear-gradient(
    45deg,
    #04619f,
    #000000);
  background: var(--background);
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  min-height: 100vh;
  -webkit-animation: fadeIn 6s;
          animation: fadeIn 6s;
  z-index: -1;
}

